import React, { Suspense, lazy } from 'react';
import { BrowserRouter, Switch, Route } from 'react-router-dom';

const Routes = () => {
    return (
        <BrowserRouter>
            <Suspense fallback="Loading...">
                <Switch>
                    <Route exact path="/" component={lazy(() => import(`./pages/Home`))} />
                    <Route exact path="/feeds" component={lazy(() => import(`./pages/Home/feeds`))} />
                    <Route exact path="/profile" component={lazy(() => import(`./pages/Home/Profile`))} />
                    <Route exact path="/responsive" component={lazy(() => import(`./pages/Home/Responsive`))} />
                    <Route exact path="/props" component={lazy(() => import(`./pages/Props/Away`))} />
                    <Route exact path="/chats" component={lazy(() => import(`./pages/Home/Chats`))} />
                    <Route exact path="/charts" component={lazy(() => import(`./pages/Props/charts`))} />
                    <Route exact path="/LogIn" component={lazy(() => import(`./pages/facebook/LogIn`))} />
                    <Route exact path="/Home" component={lazy(() => import(`./pages/facebook//Home`))} />
                    <Route exact path="/project" component={lazy(() => import(`./pages/project`))} />
                    <Route exact path="/company" component={lazy(() => import(`./pages/company`))} />
                    <Route exact path="/figma" component={lazy(() => import(`./pages/figma`))} />
                    <Route exact path="/figma_structure" component={lazy(() => import(`./pages/FigmaStructure`))} />
                </Switch>
            </Suspense>
        </BrowserRouter>
    )
}


export default Routes